import { AuthService } from './../../services/auth.service';
import { Component, OnInit } from '@angular/core';

import { I18nService } from '../../services/i18n.service';
import { MainPageService } from '../../services/carol-querys/main-page.service';

import { getShortMonthString } from '../../utils';

import * as jp from 'jsonpath/jsonpath';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'main-page',
  templateUrl: './main-page.component.html',
  styleUrls: ['./main-page.component.css']
})
export class MainPageComponent implements OnInit {
  dadosCustomersSituation;
  dadosCustomersTop;

  topResultBoxes;

  totalFidCustomers = '';
  totalNotFidCustomers = '';

  seriesFidCustomers = [];
  categoriesFidCustomers = [];

  constructor(private mainPageService: MainPageService,
              private i18n: I18nService) { }

  ngOnInit() {

    this.topResultBoxes = [{
      title: '0',
      content: 'Orders',
      color: '#00C6D3',
      percentageValue: '',
      percentageText: ''
    },
    {
      title: '0',
      content: 'Customers',
      color: 'pink',
      percentageValue: '50',
      percentageText: ''
    }];

    this.newFilter();
  }

  labelContent(e: any): string {
    return e.category;
  }

  newFilter() {
    this.mainPageService.runNamedQuery('customerPerSituation', {}).subscribe(result => {
        if(result != undefined && result['aggs'] != undefined && result['aggs']['status'] != undefined && result['aggs']['status']['buckets'] != undefined) {
            var dados = result['aggs']['status']['buckets'];
            this.dadosCustomersSituation = [];

            for (var key in dados) {
               if (dados.hasOwnProperty(key)) {
                  var valor = dados[key]['docCount'];

                  if(valor != 0) {
                    valor = valor/result["totalHits"];
                  }

                  this.dadosCustomersSituation.push({kind: key, share: valor});
                  console.log(key, dados[key]);
               }
            }
        }
    }, error => {
        console.log('error customerPerSituation', error);
    });

    function compare(a,b) {
      if (a.data < b.data)
        return -1;
      if (a.data > b.data)
        return 1;
      return 0;
    }

    this.mainPageService.runNamedQuery('customersTop', {}).subscribe(result => {
        this.dadosCustomersTop = [];

        if(result != undefined) {
            if(result["aggs"] != undefined) {
                if(result["aggs"]["name"] != undefined) {
                    if(result["aggs"]["name"]["buckets"] != undefined) {
                        var dados = result["aggs"]["name"]["buckets"];

                        for(var key in dados) {
                            if(dados.hasOwnProperty(key)) {
                                var row = dados[key];

                                this.dadosCustomersTop.push({name: row["key"], data: [row["aggregations"]["risk"]["value"]]});
                                console.info(key);
                            }
                        }

                        this.dadosCustomersTop.sort(compare);
                    }
                }
            }
        }
    }, error => {
        console.log('error customersTop', error);
    });

    this.mainPageService.runNamedQuery('QuantityOrders', {}).subscribe(result => {
        this.topResultBoxes[0]['title'] = result['totalHits'];
    }, error => {
        console.log('error getAverageTicket', error);
    });

    this.mainPageService.runNamedQuery('QuantityCustomers', {}).subscribe(result => {
        console.info(JSON.stringify(result));
        this.topResultBoxes[1]['title'] = result['totalHits'];
    }, error => {
        console.log('error getAverageTicket', error);
    });

  }
}



