import { Injectable } from '@angular/core';

import { HttpRequestsService } from '../../services/http-requests.service';

@Injectable()
export class ToolbarSearchService {

  constructor(private request: HttpRequestsService) { }

  getCampaigns(value) {
    const url = 'queries/named/findCampaigns?indexType=MASTER&pageSize=0&sortOrder=ASC&scrollable=false';

    /**
     * camp_name - Campaign Name, string (optional)
     * camp_id - Campaign ID, string (optional)
     * count - number of matches for the query , (optional)
     */
    const params = {
      count: '50',
      camp_name: value,
    };

    return this.request.post(url, params);
  }

  getCampaignByProduct(value) {
    const url = 'queries/named/findCampaignsByProduct?indexType=MASTER&pageSize=0&sortOrder=ASC&scrollable=false';

    /**
     * prod_name - Product Name, string (optional)
     * prod_id - Product ID, string (optional)
     * count - number of matches for the query, , (optional)
     */
    const params = {
     count: '50',
     prod_name: value,
    };

    return this.request.post(url, params);
  }


}



