import { Component, OnInit, Renderer2, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';

import { I18nService } from './../../services/i18n.service';
import { AuthService } from '../../services/auth.service';
import { ToolbarSearchService } from '../../services/carol-querys/toolbar-search.service';

import 'rxjs/add/operator/debounceTime';
import * as jp from 'jsonpath/jsonpath';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.css']
})
export class ToolbarComponent implements OnInit {

  showUserDropdown = false;
  showLanguageDropdown = false;
  showSearchBox = false;

  language;
  searchInput = '';
  face = 'face.png';
  user;

  campaigns = [];
  products = [];
  searchingCampaigns = false;
  searchingProducts = false;

  @ViewChild('inputElement', {read: ElementRef}) inputElement: ElementRef;

  constructor(private i18n: I18nService,
              private searchService: ToolbarSearchService,
              private authService: AuthService,
              private renderer: Renderer2,
              private router: Router) {
    renderer.listen('document', 'click', (event: MouseEvent) => {
      this.wasClickedOutOfSearchBox(event);
    });
  }

  ngOnInit() {
    this.language = this.i18n.getLanguage();
    const email = localStorage.getItem('user');
    this.user = email.substring(0, email.indexOf('@'));
  }

  changeLanguage(language) {
    this.language = language;
    this.i18n.setLanguage(language);
  }

  logout() {
    this.authService.logout();
  }

  changeSearch(value) {
    if (value.length < 3) {
      return;
    }

    this.showSearchBox = true;
    this.searchingCampaigns = true;
    this.searchingProducts = true;

    this.campaigns = [];
    this.products = [];

    this.searchService.getCampaigns(value)
      .subscribe(
        result => {
          console.log('getCampaigns', result);

          this.campaigns = jp.query(result, '$..campaign.buckets.*')
            .map(function(x) {
              return {
                label: jp.query(x, '$..aggregations..key'),
                value: x['key'],
              };
            });

          setTimeout(() => {
            this.searchingCampaigns = false;
          }, 5000);
        },
        error => {
          console.log('error getCampaigns', error);
          this.searchingCampaigns = false;
     });

    this.searchService.getCampaignByProduct(value)
      .subscribe(
        result => {
          console.log('getCampaignByProduct', result);

          this.products = jp.query(result, '$..campaign.buckets.*')
            .filter(x => x['resolved'])
            .map(function(x) {
              return {
                label: jp.query(x, '$..aggregations..key'),
                value: x['key'],
              };
            });

          setTimeout(() => {
            this.searchingProducts = false;
          }, 5000);
        },
        error => {
          console.log('error getCampaignByProduct', error);
          this.searchingProducts = false;
     });
  }

  wasClickedOutOfSearchBox(event) {
    if (!this.inputElement.nativeElement.contains(event.target)) {
      this.showSearchBox = false;
    }
  }

}



