import { Injectable } from '@angular/core';

import { HttpRequestsService } from '../../services/http-requests.service';

@Injectable()
export class MainPageService {

  constructor(private request: HttpRequestsService) { }

  runNamedQuery(namedQuery, params) {
    const url = 'queries/named/' + namedQuery + '?indexType=MASTER&pageSize=0&sortOrder=ASC&scrollable=false';

    return this.request.post(url, params);
  }

  private getStringDate(date) {
    return (date instanceof Date) ? date.toISOString().substring(0, 10) : date;
  }

}



