import { Routes, RouterModule } from '@angular/router';

import { MainPageComponent } from './pages/main/main-page.component';
import { LoginComponent } from './pages/login/login.component';
import { CampaignsPageComponent } from './pages/campaigns/campaigns-page.component';
import { CampaignDetailPageComponent } from './pages/campaign-detail/campaign-detail-page.component';
import { ProductDetailPageComponent } from './pages/product-detail/product-detail-page.component';
import { AuthGuard } from './services/auth-guard.service';
import { AuthGuardLogin } from './services/auth-guard-login.service';

const appRoutes: Routes = [
{ path: '', component: MainPageComponent, canActivate: [AuthGuard] },
{ path: 'login', component: LoginComponent, canActivate: [AuthGuardLogin] },
{ path: 'campaigns', component: CampaignsPageComponent, canActivate: [AuthGuard] },
{ path: 'campaign/:id', component: CampaignDetailPageComponent, canActivate: [AuthGuard] },
{ path: 'product/:id', component: ProductDetailPageComponent, canActivate: [AuthGuard] },
{ path: '**', redirectTo: '' },
];

export const routing = RouterModule.forRoot(appRoutes);



